@font-face {
  font-family: 'Code Pro Light';
  src: url("../fonts/Code Pro Light.otf");
}

@font-face {
  font-family: 'Din Next Light';
  src: url("../fonts/8234.ttf");
}

* {
  padding: 0;
  margin: 0;
}

html, body {
  height: 100%;
  box-sizing: border-box;
}

.page__wrapper {
  min-height: 100%;
  margin-bottom: -50px;
}

* html .page__wrapper {
  height: 100%;
}

.page__buffer {
  height: 50px;
}

.header {
  position: relative;
  top: 0;
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  height: 76px;
  align-items: flex-end;
}

.indexbg {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
}

.head-menu {
  width: 688px;
  position: relative;
}

.menu {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.menu-item {
  display: inline;
}

.menu-item a {
  text-align: center;
  text-decoration: none;
  padding: 0 10px;
  color: #fff;
  transition: color 0.4s ease 0s;
  font-size: 10px;
}

.menu-item a:hover {
  color: #eb6608;
}

.social {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: -76px;
  right: -130px;
}

.social-item {
  list-style: none;
  width: 27px;
}

.social--presentation, .social--contacts, .social--in, .social--vk, .social--youtube {
  background-size: cover;
  background-position: center top;
  margin-bottom: 10px;
}

.social--presentation {
  height: 76px;
  background-image: url("../../static/img/general/d.png");
}

.social--contacts, .social--in, .social--vk, .social--youtube {
  height: 27px;
}

.social--in {
  background-image: url("../../static/img/general/in.png");
}

.social--vk {
  background-image: url("../../static/img/general/vk.png");
}

.social--youtube {
  background-image: url("../../static/img/general/yut.png");
}

.social--contacts {
  background-image: url("../../static/img/general/cont.png");
}

@media screen and (max-width: 960px) {
  .head-menu {
    width: 500px;
  }
  .social {
    right: -40px;
  }
}

.slide1 {
  height: 100vh;
  min-height: 720px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.biglogo {
  width: 291px;
  margin: 50px 0;
}

.biglogo-img {
  width: 100%;
}

.tagline {
  color: #fff;
  text-align: center;
  font-family: "Code Pro Light";
  font-size: 83px;
  line-height: .5em;
  margin: 150px 0;
}

.tagline span {
  font-size: 21px;
}

.faces {
  position: absolute;
  left: 110px;
  bottom: 110px;
  width: 129px;
}

.faces-img {
  width: 100%;
}

@media screen and (max-width: 960px) {
  .tagline {
    margin: 100px 0;
    line-height: 1em;
    font-size: 43px;
  }
  .biglogo {
    margin: 10px;
  }
}

.slide2 {
  height: 586px;
  margin-bottom: 20px;
  display: flex;
}

.slide2-left, .slide2-right {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide2-left {
  background: #2f2e2e;
}

.slide2-left-text {
  font-family: "Din Next Light";
  font-size: 21px;
  color: #fff;
  width: 474px;
  line-height: 1em;
  text-align: justify;
}

.slide2-right {
  background: url("../../static/img/general/bg1.png") no-repeat left;
  background-size: cover;
}

@media screen and (max-width: 960px) {
  .slide2 {
    flex-direction: column;
  }
}
