.slide1 {
    height: 100vh;
    min-height: 720px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.biglogo {
    width: 291px;
    margin: 50px 0;
    &-img {
        width: 100%;
    }
}
.tagline{
    color: $color1;
    text-align: center;
    font-family:"Code Pro Light";
    font-size: 83px;
    line-height: .5em;
    margin: 150px 0;
    span{
        font-size: 21px;
    }

}
.faces {
    position: absolute;
    left: 110px;
    bottom: 110px;
    width: 129px;
    &-img {
        width: 100%;
    }

}
@media screen and (max-width: 960px) {
    .tagline{
        margin: 100px 0;
        line-height: 1em;
        font-size: 43px;
    }
    .biglogo {
        margin: 10px;
    }
}
