.slide2 {
    height: 586px;
    margin-bottom: 20px;
    display: flex;
    &-left, &-right {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

    }
    &-left {
        background: $color2;
        &-text {
            font-family: "Din Next Light";
            font-size: 21px;
            color: $color1;
            width: 474px;
            line-height: 1em;
            text-align: justify
        }

    }
    &-right {
        background: url("../../static/img/general/bg1.png") no-repeat left;
        background-size: cover;
    }
}


@media screen and (max-width: 960px) {
    .slide2{
        flex-direction: column;
    }
}
