.head-menu {
    width: 688px;
    position: relative;
}

.menu {

    display: flex;
    width: 100%;
    justify-content: space-between;
    &-item {
        display: inline;
        a {
            text-align: center;
            text-decoration: none;
            padding: 0 10px;
            color: $color1;
            transition: color 0.4s ease 0s;
            font-size: 10px;
            &:hover {
                color: $color3;
            }
        }
    }
}

.social {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: -76px;
    right: -130px;
    &-item {
        list-style: none;
        width: 27px;

    }
    &--presentation, &--contacts, &--in, &--vk, &--youtube {
        background-size: cover;
        background-position: center top;
        margin-bottom: 10px;

    }
    &--presentation {
        height: 76px;
        background-image: url("../../static/img/general/d.png");

    }
    &--contacts, &--in, &--vk, &--youtube {
        height: 27px;
    }
    &--in {
        background-image: url("../../static/img/general/in.png");
    }
    &--vk {
        background-image: url("../../static/img/general/vk.png");
    }
    &--youtube {
        background-image: url("../../static/img/general/yut.png");
    }
    &--contacts {
        background-image: url("../../static/img/general/cont.png");
    }

}

@media screen and (max-width: 960px) {
    .head-menu {
        width: 500px;
    }
    .social {
        right: -40px;
    }
}
